}
.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links li:active {
  font-weight: bold !important;
}

.nav-links a {
  color: #292929;
  text-decoration: none;
  font-size: 20px;
  padding: 0.5rem;
}

.nav-links a:active {
  font-weight: bold;
  color: black;
}

.nav-links a:hover {
  font-weight: bold;
  color: black;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links a {
    font-size: 15px;
    color: white !important;
    font-weight: 550;
  }
}

@media (min-width: 820px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: white !important;
    text-decoration: none;
    font-weight: 550;
    font-size: 20px;
  }

  .nav-links a:hover {
    color: white !important;
    text-decoration: none;
    border: 1px solid white;
  }
  
}

.side-drawer {
