.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #000;
  border-radius: 4px;
  background: #000;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover {
  background: #152083;
  border-color: #152083;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.button:active {
  background: #152083;
  border-color: #152083;
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
